<script>
// import HelloWorld from './components/HelloWorld.vue'
import axios from 'axios'
import Title from './components/title'

export default {
  name: 'App',
  components: {
    Title
  },
  created () {
    this.getDataList();
    this.getBusi();
    this.getpkData();
           },
  methods:{
    handleClick(row){
      console.log(row);
    },
    changeTab(index,event){
      this.currentTab = index;
      console.log(event);
    },
    getDataList(){
      axios.get("http://112.35.56.85:8888/xs/reporter/score_single.php",
      {
        headers: {
        'Content-Type': 'application/json; charset=utf-8'
        }})
      .then(response=>{
        console.log(response.data);
        this.mockdata = response.data;
      })
      .catch(error=>{
        console.log(error);
      })
    },
    getBusi(){
      axios.get("http://112.35.56.85:8888/xs/reporter/score_buzi.php",{
        headers: {
        'Content-Type': 'application/json; charset=utf-8'
        }})
        .then(response=>{
          // console.log(response.data.data);
          let data = response.data.data;
          // for(var i=0; i < data.length; i++){
          //     let obj = {};
          //     obj.county = data[i][0];
          //     obj.aim = data[i][1];
          //     obj.number = data[i][2];
          //     obj.discount = data[i][3];
          //     obj.progress = data[i][4];
          //     obj.score = data[i][5];
          //     console.log(obj);
          //     this.tableData.push(obj);
          // }
          for(var i of data){
            let obj = {};
            obj.county = i[0];
            obj.aim = i[1]
            obj.number = i[2]
            obj.discount = i[3];
            obj.progress = i[4];
            obj.score = i[5];
            this.tableData.push(obj);
            // console.log(i);
          }
        })
    },
    getpkData(){
      axios.get("http://112.35.56.85:8888/xs/reporter/score_pk.php",{
        headers: {
        'Content-Type': 'application/json; charset=utf-8'
        }})
        .then(response=>{
          let data = response.data.data;
          for(var i of data){
            let obj = {};
            obj.county = i[0];
            obj.aim = i[1]
            obj.number = i[2]
            obj.discount = i[3];
            obj.progress = i[4];
            obj.score = i[5];
            this.pkData.push(obj);
          }
        })
    },
    getTime(){
      let time = new Date();
      this.currentTime = time.getHours() + ':00:00';
    }
  },
  mounted(){
    this.getTime();
  },
  data() {
    return {
      // data: '',
      currentTab: 0,
      currentTime: 0,
